import { Typography, Container, List, ListItem, ListItemText, ListItemButton, AppBar, Tab, Tabs, Button } from '@mui/material';
import React from 'react';
import { format } from 'date-fns';
import { Link, useLocation } from 'react-router-dom';
import RefreshIcon from '@mui/icons-material/Refresh';

interface Props {
  uutiset : Data[];
  haeData : () => void;
  uutisetVirhe : string;
}

const Uutiset : React.FC<Props> = (props : Props) : React.ReactElement => {

  const sijainti = useLocation();

  return (
    <Container maxWidth="sm" sx={{marginBottom:"20px"}}>
      
      <AppBar position="static">
        <Tabs textColor="inherit" variant="fullWidth" value={sijainti.pathname}>
          <Tab label="Uutiset" value="/" to="/" component={Link}/>
          <Tab label="Blogikirjoitukset" value="/kirjoitukset" to="/kirjoitukset" component={Link}/>
        </Tabs>
      </AppBar>

      {Boolean(!props.uutisetVirhe)
      ? 
        <List>
          {props.uutiset.map((uutinen : Data, idx : number) => {
          return (
            <ListItem key={idx} disablePadding>
              <ListItemButton component={Link} to={`/uutinen/${uutinen.id}`}>
                <ListItemText primary={uutinen.title} secondary={format(uutinen.published_at, "dd.MM.y")}/>
              </ListItemButton>
            </ListItem>
          )})}
        </List>
      : 
        <Typography sx={{marginTop:"20px"}}>{props.uutisetVirhe}</Typography>
      }
      
      <Button
      onClick={props.haeData}
      sx={{marginLeft:"15px", marginTop:"10px"}}
      variant='contained'
      startIcon={<RefreshIcon />}>
        Päivitä
      </Button>
    </Container>
    
  );
}

export default Uutiset;
