import { Typography, Container, Button, Box } from '@mui/material';
import React from 'react';
import { format } from 'date-fns';
import { useParams, Link } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

interface Props {
  uutiset : Data[]
}

const Uutinen : React.FC<Props> = (props : Props) : React.ReactElement => {

  const { id } = useParams<any>();

  const valittuUutinen : Data | undefined = props.uutiset.find((uutinen : Data) => {
    return uutinen.id === Number(id)
  });

  return (
    <Container maxWidth="sm">

      {valittuUutinen ? (
        <>
        <Typography variant="h5">{valittuUutinen!.title}</Typography>
        <Typography>{format(valittuUutinen!.published_at, "dd.MM.y")}</Typography>
        <Box component="img" sx={{width:"100%"}} src={valittuUutinen!.image_url}/>
        <Typography variant="h6">{valittuUutinen!.news_site}</Typography>
        <Typography>{valittuUutinen!.summary}</Typography>
        <Typography sx={{marginTop:"10px"}}><a href={valittuUutinen!.url}>Uutiseen</a></Typography>
        </>
      )
      : (
        <Typography>Uutista ei löytynyt</Typography>
      )}
      
      <Button
      sx={{marginTop:"20px"}}
      variant='contained'
      component={Link}
      to="/"
      startIcon={<ArrowBackIcon />}>
      Takaisin
      </Button>
    </Container> 

  );
}

export default Uutinen;
