import React, { useEffect, useRef, useState } from 'react';
import { Route, Routes } from 'react-router-dom'
import Uutiset from './components/Uutiset';
import Uutinen from './components/Uutinen'
import Kirjoitukset from './components/Kirjoitukset';
import Kirjoitus from './components/Kirjoitus'
import { Container } from '@mui/material';

const App : React.FC = () : React.ReactElement => {

  const pyyntoLahetetty : React.MutableRefObject<boolean> = useRef(false);

  const [uutiset, setUutiset] = useState<Data[]>([]);
  const [kirjoitukset, setKirjoitukset] = useState<Data[]>([]);
  const [uutisetVirhe, setUutisetVirhe] = useState<string>("");
  const [kirjoituksetVirhe, setKirjoituksetVirhe] = useState<string>("");

  const haeData = async () : Promise<void> => {

    try {

      const uutisetYhteys : Response = await fetch(`https://api.spaceflightnewsapi.net/v4/articles/?limit=10`);
      const uutisetResponseData : any = await uutisetYhteys.json();
      const apuUutiset : Data[] = uutisetResponseData.results.map((result : any) => ({
        id : result.id,
        title : result.title,
        published_at : result.published_at,
        image_url : result.image_url,
        news_site : result.news_site,
        summary : result.summary,
        url : result.url,
      }));
      setUutiset([...apuUutiset]);
      setUutisetVirhe("");

      const kirjoituksetYhteys : Response = await fetch(`https://api.spaceflightnewsapi.net/v4/blogs/?limit=10`);
      const kirjoituksetResponseData : any = await kirjoituksetYhteys.json();
      const apuKirjoitukset : Data[] = kirjoituksetResponseData.results.map((result : any) => ({
        id : result.id,
        title : result.title,
        published_at : result.published_at,
        image_url : result.image_url,
        news_site : result.news_site,
        summary : result.summary,
        url : result.url,
      }));
      setKirjoitukset([...apuKirjoitukset]);
      setKirjoituksetVirhe("");

    } catch (e: any) {
      setUutisetVirhe("Palvelimelle ei saada yhteyttä.");
      setKirjoituksetVirhe("Palvelimelle ei saada yhteyttä.")
    }
  } 

  useEffect(() => {

    if (!pyyntoLahetetty.current) {
      haeData();
    }

    return () => {
      pyyntoLahetetty.current = true;
    }

  }, []);

  return (
    <>
    <Container maxWidth="sm">

    <Routes>

      <Route path="/" element={<Uutiset uutiset={uutiset} haeData={haeData} uutisetVirhe={uutisetVirhe} />} />
      <Route path="/kirjoitukset" element={<Kirjoitukset kirjoitukset={kirjoitukset} haeData={haeData} kirjoituksetVirhe={kirjoituksetVirhe} />} />
      <Route path="/uutinen/:id" element={<Uutinen uutiset={uutiset} />} />
      <Route path="/kirjoitus/:id" element={<Kirjoitus kirjoitukset={kirjoitukset} />} />

    </Routes>

    </Container>
    </>
  );
}

export default App;
