import { Typography, Container, Button, Box } from '@mui/material';
import React from 'react';
import { format } from 'date-fns';
import { useParams, Link } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

interface Props {
  kirjoitukset : Data[]
}

const Kirjoitus : React.FC<Props> = (props : Props) : React.ReactElement => {

  const { id } = useParams<any>();

  const valittuKirjoitus : Data | undefined = props.kirjoitukset.find((kirjoitus : Data) => {
    return kirjoitus.id === Number(id)
  });

  return (
    <Container maxWidth="sm">

      {valittuKirjoitus ? (
        <>
        <Typography variant="h5">{valittuKirjoitus!.title}</Typography>
        <Typography>{format(valittuKirjoitus!.published_at, "dd.MM.y")}</Typography>
        <Box component="img" sx={{width:"100%"}} src={valittuKirjoitus!.image_url}/>
        <Typography variant="h6">{valittuKirjoitus!.news_site}</Typography>
        <Typography>{valittuKirjoitus!.summary}</Typography>
        <Typography sx={{marginTop:"10px"}}><a href={valittuKirjoitus!.url}>Blogikirjoitukseen</a></Typography>
        </>
      )
      : (
        <Typography>Blogikirjoitusta ei löytynyt</Typography>
      )}

      <Button
      sx={{marginTop:"20px"}}
      variant='contained'
      component={Link}
      to="/kirjoitukset"
      startIcon={<ArrowBackIcon />}>
      Takaisin
      </Button>
    </Container> 

  );
}

export default Kirjoitus;
