import {Typography, Container, List, ListItem, ListItemText, ListItemButton, AppBar, Tab, Tabs, Button } from '@mui/material';
import { format } from 'date-fns';
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import RefreshIcon from '@mui/icons-material/Refresh';

interface Props {
  kirjoitukset : Data[];
  haeData : () => void;
  kirjoituksetVirhe : string;
}

const Kirjoitukset : React.FC<Props> = (props : Props) : React.ReactElement => {

  const sijainti = useLocation();

  return (
    <Container maxWidth="sm" sx={{marginBottom:"20px"}}>

      <AppBar position="static">
        <Tabs textColor="inherit" variant="fullWidth" value={sijainti.pathname}>
          <Tab label="Uutiset" value="/" to="/" component={Link}/>
          <Tab label="Blogikirjoitukset" value="/kirjoitukset" to="/kirjoitukset" component={Link}/>
        </Tabs>
      </AppBar>

      {Boolean(!props.kirjoituksetVirhe)
      ?
        <List>
          {props.kirjoitukset.map((kirjoitus : Data, idx : number) => {
          return (
            <ListItem key={idx} disablePadding>
              <ListItemButton component={Link} to={`/kirjoitus/${kirjoitus.id}`}>
                <ListItemText primary={kirjoitus.title} secondary={format(kirjoitus.published_at, "dd.MM.y")}/>
              </ListItemButton>
            </ListItem>
          )})}
        </List>
      :
        <Typography sx={{marginTop:"20px"}}>{props.kirjoituksetVirhe}</Typography>
      }

      <Button
      onClick={props.haeData}
      sx={{marginLeft:"15px", marginTop:"10px"}}
      variant='contained'
      startIcon={<RefreshIcon />}>
        Päivitä
      </Button>
    </Container>
    
  );
}

export default Kirjoitukset;
